import React from "react";
import "./instruction.css";
import { useNavigate } from "react-router-dom";

export function Instruction({
	questionsCount,
	answersList,
}: {
	questionsCount: number;
	answersList: string[];
}) {
	const navigate = useNavigate();

	const startQuizButtonText = answersList.length
		? "Wr do quizu"
		: "Rozpocz quiz";

	// on start quiz click handler
	function startQuizOnClick() {
		navigate("/quiz");
	}

	return (
		<main className="instruction">
			<h2 className="instruction__title">
				Witaj w Quizie o technologiach internetowych
			</h2>

			<ol className="instruction__rules">
				<li>
					Dostpne bdzie {questionsCount} pyta z czterema moliwymi
					odpowiedziami, gdzie uytkownik bdzie mg zaznaczy jedn, wybran
					przez siebie odpowied.
				</li>

				<li>
					Pytania bd wywietlay si kolejno, po udzieleniu na nie odpowiedzi
					(klikniciu "Dalej").
					<ol>
						<li>
							Wybr odpowiedzi musi by dokonany, nie mona nie udzieli
							odpowiedzi / pomin pytania.
						</li>

						<li>Mona wrci do poprzednich pyta i zmieni odpowied.</li>
					</ol>
				</li>

				<li>Dane o quize przechowywane w Session Storage</li>

				<li>
					Pod koniec quizu uytkownik zobaczy swj wynik (liczba punktw + PASS
					/ FAIL). Quiz zalicza 75% poprawnych odpowiedzi.
				</li>

				<li>
					Po zakoczeniu quizu istnieje moliwo pobrania wyniku wraz z
					odpowiedziami (w postaci pliku .pdf) - na wzr potwierdzenia
					ukoczenia quizu, certyfikatu.
				</li>

				<li>
					Za kadym razem, gdy uytkownik ponownie uruchamia quiz, kolejno
					pyta i odpowiedzi jest losowa.
				</li>
			</ol>

			<div className="instruction__button">
				<button onClick={startQuizOnClick}>{startQuizButtonText}</button>
			</div>
		</main>
	);
}
